import React, { Component } from 'react'
import Link from './common/Link'
import face from './img/icons/face.png'
import insta from './img/icons/insta.png'
import github from './img/icons/github.png'
import email from './img/icons/email.png'

export class Footer extends Component {
    render() {
        return (
            <div className="footer">
                <div className="links">
                    <Link icon={face} href="https://www.facebook.com/abdalrahman.daas.16" />
                    <Link icon={insta} href="https://instagram.com/abdalrahmandaas?utm_medium=copy_link" />
                    <Link icon={github} href="https://github.com/AbdAlrahmanDaas963" />
                    <Link icon={email} href="mailto:abdalrahmandaas963@gmail.com" />
                    {/* <Link icon={email} href="https://t.me/abdalrahman_daas" /> */}
                </div>
            </div>
        )
    }
}

export default Footer
