import React, { Component } from 'react'

export class Content extends Component {
    render() {
        const {title, by, img, href} = this.props;
        return (
            <div className="item col-sm-12 col-md-6 col-lg-3">
                
                <a className="a-item" href={href} target="_blank" rel="noopener noreferrer">
                    <img src={img} alt="" />
                    <h4>{title}</h4>
                    <h6>{by}</h6>
                </a>
            </div>
        )
    }
}

export default Content
