import React, { Component } from 'react'
import Content from './Content'
import Tic from './img/Tic.png';
import Great from './img/Great.png';
import meca from './img/mecaGame.png'

export class Works extends Component {
    render() {
        return (
            <div className="container works">
                    <h2 className="work">My latest works :</h2>
                <div className="content row">
                    <Content href="https://abdalrahmandaas963.github.io/tic-tac-toe/" img={Tic} by={"{ React-js }"} title={"Tic-Tac-Toe"} />
                    <Content href="http://greatcogroup.com/" img={Great} by={"{ Vanilla }"} title={"GreatCo"} />
                    <Content href="https://abdalrahmandaas963.github.io/MemoryCardGame2/" img={meca} by={"{ React-js }"} title={"Memory Card Game"} />
                </div>
            </div>
        )
    }
}

export default Works
